<script>
    import {
        searchDictionaryManList,
        addUpdateDictionaryManList,
        deleteDictionaryManList,
        searchDictionarySelectList,
        judgeDictionaryKey,

        QuerypersonnelBaseInfo,
        getDepartmentDialogData,
        getpostManageList,
        addpersonnelBaseInfo,
        deletepersonnelBaseInfo
    } from '../../../services/Manage/postManage.js'
    import API from '../../../api'
    import {
        mapState
    } from 'vuex'
    // import saveAs from 'file-saver';
    // var FileSaver = require('file-saver');
    // import XLSX from 'xlsx';
    import 'xlsx-style'
    // import 'xlsx-style'
    export default {
        data() {
            var KeyFlag = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error(''));
                } else {
                    if (this.oldKey != value) {
                        judgeDictionaryKey(this.updateIndex, this.form.type, value).then(result => {
                            if (!result) {
                                return callback(new Error(''));
                            } else {
                                return callback()
                            }
                        })
                    } else {
                        return callback()
                    }

                }
            };
            return {
                SavepersonnelBaseInfo_API:API.PRO_HOST + API.SavepersonnelBaseInfo_API,
                loading: true,
                display: true,
                display2: false,
                formLabelWidth: '130px',
                fileListData: [],
                hiredate: [],
                leaveDate: [],
                conversionDate: [],
                inputArr: {
                    empName: '',
                    //        
                    department: '',
                    //       
                    title: '',
                    //   

                    hiredateFrom: '',
                    hiredateTo: '',

                    conversionDateFrom: '',
                    conversionDateTo: '',
                    //       
                    idCard: '',
                    //          
                    sex: '',
                    //       
                    birthday: '',
                    //        
                    householdRegistration: '',
                    //       
                    company: '',
                    //        
                    contractFiled: '',

                    //            
                    firstDegree: '',
                    //        
                    insuranceDate: '',
                    //       
                    leaveDateFrom: '',
                    leaveDateTo: '',
                    deleteFlag: '0'

                },
                // 
                selectDatahkxz: [], //
                selectDatassgs: [], //
                selectDataxl: [], //
                selectDatagz: [], //
                departmantIdArr: [],
                transferDataDep: [],
                selectData: [{
                    name: "type",
                    options: [{
                        value: null,
                        label: ''
                    }]
                }],
                // 
                selectObj: {
                    type: "",
                },
                dataModel: [], //
                minBtns: ['Add'], //
                allBtns: [], // 
                labelPosition: 'right',
                tableData: [],
                heightItem: window.innerHeight - 260, // 
                dialogFormVisible: false, //
                form: {}, // 
                formData: {
                    id: '',
                    empName: '',
                    //        
                    department: '',
                    //       
                    title: '',
                    //   
                    hiredate: '',
                    //      
                    conversionDate: '',
                    //       
                    idCard: '',
                    //          
                    sex: '',
                    //       
                    birthday: '',
                    //        
                    householdRegistration: '',
                    //       
                    company: '',
                    //        
                    contractFiled: '',
                    //        
                    contractDateFrom: '',
                    //        
                    contractDateTo: '',
                    //        
                    firstDegreeSchool: '',
                    //            
                    firstDegree: '',
                    //        
                    insuranceDate: '',
                    //       
                    leaveDate: '',
                    //        
                    reasonsForLeaving: '',
                    //       
                    contactPhone: '',
                    //       
                    emergencyContact: '',
                    //        
                    nativePlace: '',
                    //       
                    currentResidence: '',
                    //        
                    typeOfWork: '',
                    //       
                    certificateName: '',
                    //      
                    certificateNumber: '',
                    //      
                    annotation: '',

                    userId: this.$store.state.user.userId // ID
                },
                formIndex: -1,
                oldform: {}, // 
                oldKey: '', // formkey
                updateIndex: '',
                dialogTitle: '', //title
                typeSelectArr: [], // 
                rules: {
                    empName: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    department: [{
                        required: true,
                        message: '',
                        trigger: 'change'
                    }],
                    title: [{
                        required: true,
                        message: '',
                        trigger: 'change'
                    }],
                    hiredate: [{
                        type: 'string',
                        required: true,
                        message: '',
                        trigger: 'change'
                    }],
                    idCard: [{
                            required: true,
                            message: 'ID',
                            trigger: 'blur'
                        },
                        {
                            validator: this.validID,
                            trigger: 'blur'
                        }
                        // {
                        //     pattern: /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/,
                        //     message: ''
                        // }
                    ],

                    // title: [{
                    //     required: true,
                    //     message: '',
                    //     trigger: 'change'
                    // }, {
                    //     type: 'number',
                    //     message: '',
                    //     trigger: 'blur'
                    // }],

                },
                wopts: {
                    bookType: 'xlsx',
                    bookSST: true,
                    type: 'binary',
                    cellStyles: true
                },
            }

        },
        watch: {
            heightItem(val) {
                if (!this.timer) {
                    this.heightItem = val
                    this.timer = true
                    const that = this
                    setTimeout(function () {
                        that.timer = false
                    }, 400)
                }
                deep: true
            },
        },
        computed: {
