                    {
                        wpx: 120
                    }, //
                    {
                        wpx: 100
                    }, //
                    {
                        wpx: 100
                    }, //
                    {
                        wpx: 100
                    }, //
                    {
                        wpx: 90
                    }, //
                    {
                        wpx: 80
                    }, //
                    {
                        wpx: 100
                    }, //
                    {
                        wpx: 100
                    }, //
                    {
                        wpx: 80
                    }, //
                    {
                        wpx: 150
                    }, //
                    {
                        wpx: 120
                    }, //
                    {
                        wpx: 100
                    }, //
                    {
                        wpx: 100
                    }, //
                    {
                        wpx: 50
                    }, //
                    {
                        wpx: 120
                    }, //
                    {
                        wpx: 120
                    }, //
                    {
                        wpx: 150
                    }, //
                ];
                tmpdata["!rows"] = [ //
                    {
                        hpt: 100
                    }, //
                    


                ];
                var tmpWB = {
                    SheetNames: [''], //
                    Sheets: {
                        '': Object.assign({},
                            tmpdata, //
                            {
                                '!ref': outputPos[0] + ':' + outputPos[outputPos.length - 1] //
                            })
                    }
                };
                var tmpDown = new Blob([this.s2ab(XLSX.write(tmpWB, {
                        bookType: (type == undefined ? 'xlsx' : type),
                        bookSST: false,
                        type: 'binary'
                    } //
                ))], {
                    type: ""
                });
                this.saveAs2(tmpDown, '' + '.' + (this.wopts.bookType == "biff2" ? "xlsx" :
                    this.wopts.bookType))
            },
            // 26excel
            getCharCol(n) {
                let temCol = '',
                    s = '',
                    m = 0
                while (n > 0) {
                    m = n % 26 + 1
                    s = String.fromCharCode(m + 64) + s
                    n = (n - m) / 26
                }
                return s
            },
            s2ab(s) {
                if (typeof ArrayBuffer !== 'undefined') {
                    var buf = new ArrayBuffer(s.length);
                    var view = new Uint8Array(buf);
                    for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
                    return buf;
                } else {
                    var buf = new Array(s.length);
                    for (var i = 0; i != s.length; ++i) buf[i] = s.charCodeAt(i) & 0xFF;
                    return buf;
                }
            },
            //
            // 
            async validID(rule, value, callback) {
                // 1518151817X 
                let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
                if (reg.test(value)) {
                    await this.go(value.length);
                    callback()
                } else {
                    callback(new Error(''))
                }
            },

            // 
            go(val) {
                let iden = this.formData.idCard;
                let sex = null;
                let birth = null;
                let myDate = new Date();
                let month = myDate.getMonth() + 1;
                let day = myDate.getDate();
                let age = 0;

                if (val === 18) {
                    age = myDate.getFullYear() - iden.substring(6, 10) - 1;
                    sex = iden.substring(16, 17);
                    birth = iden.substring(6, 10) + "-" + iden.substring(10, 12) + "-" + iden.substring(12, 14);
                    if (iden.substring(10, 12) < month || iden.substring(10, 12) == month && iden.substring(12, 14) <=
                        day) age++;

                }
                if (val === 15) {
                    age = myDate.getFullYear() - iden.substring(6, 8) - 1901;
                    sex = iden.substring(13, 14);
                    birth = "19" + iden.substring(6, 8) + "-" + iden.substring(8, 10) + "-" + iden.substring(10, 12);
                    if (iden.substring(8, 10) < month || iden.substring(8, 10) == month && iden.substring(10, 12) <=
                        day) age++;
                }

                if (sex % 2 === 0)
                    sex = '0';
                else
                    sex = '1';
                this.formData.sex = sex;
                // this.baseInfo.age = age;
                this.formData.birthday = birth;
                // this.formData.birthplace = this.area[iden.substring(0,2)];
            },
            changeHandler() {
                var time = this.formData.hiredate;
                var time1 = new Date(time);
                var date = new Date(time1);
                let Y = date.getFullYear() + '-';
                let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1 + 2) : date.getMonth() + 1 + 2) + '-';
                let D = date.getDate() + ' ';
                this.formData.conversionDate = Y + M + D;
            }

        },
        created() {
            this.display = true;
            this.display2 = false;
            this.btns.map((item, index) => {
                if (this.minBtns.indexOf(item.name) != -1) {
                    this.allBtns.push({
                        'name': item.name,
                        'value': item.value
                    })
                }
            });
            this.selectAll();
            this.getSSBMData();
            this.seachAllData();

        },
        mounted() {
            const that = this
            window.onresize = () => { //
                return (() => {
                    that.heightItem = window.innerHeight - 160;

                })()
            };



        },


    }
</script>

